.gallery {
  background: linear-gradient(
    to bottom,
    var(--bg-second) 30%,
    var(--primari-white-color) 70%
  );

  @media screen and (min-width: 320px) {
    margin-top: -50px;
    margin-bottom: 80px;
  }
  @media screen and (min-width: 768px) {
    margin-top: -130px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 120px;
    margin-top: -130px;
  }
}

.gallery__list {
  display: grid;
  grid-template-columns: repeat(7, minmax(41px, 1fr));
  grid-template-rows: repeat(21, minmax(8px, 1fr));
  gap: 2px;

  @media screen and (min-width: 768px) {
    grid-template-columns: repeat(7, minmax(90px, 1fr));
    grid-template-rows: repeat(21, minmax(12px, 1fr));
    gap: 4px;
  }

  @media screen and (min-width: 767px) {
    grid-template-columns: repeat(7, minmax(100px, 1fr));
    grid-template-rows: repeat(21, minmax(15px, 1fr));
    gap: 10px;
  }
}

.gallery__item {
  position: relative;
}

.gallery__item:nth-child(1) {
  grid-column: 1;
  grid-row: 8;
  grid-row-end: span 6;
}

.gallery__item:nth-child(2) {
  grid-column: 2;
  grid-row: 6;
  grid-row-end: span 6;
}

.gallery__item:nth-child(3) {
  grid-column: 2;
  grid-row: 12;
  grid-row-end: span 6;
}

.gallery__item:nth-child(4) {
  grid-column: 3;
  grid-row: span 15;
  grid-column-start: span 2;
}

.gallery__item:nth-child(5) {
  grid-column: 3;
  grid-row: span 6;
}

.gallery__item:nth-child(6) {
  grid-column: 5;
  grid-row: 6;
  grid-row-end: span 6;
}

.gallery__item:nth-child(7) {
  grid-column: 5;
  grid-row: 12;
  // grid-row: 5;
  grid-row-end: span 6;
}

.gallery__item:nth-child(8) {
  grid-column: 6;
  grid-row: 5;
  grid-row-end: span 6;
}

.gallery__item:nth-child(9) {
  grid-column: 6;
  grid-row: 11;
  grid-row-end: span 6;
}

.gallery__item:nth-child(10) {
  grid-column: 7;
  grid-row: 7;
  grid-row-end: span 6;
}

.photo-box {
  position: relative;
  width: 100%;
  height: 100%;

  transition: transform 250ms ease-in-out;
}

.photo-box:hover {
  position: absolute;
  z-index: 10;
  transform: scale(135%, 135%);
}

.photo-box__image {
  position: absolute;

  width: 100%;
  height: 100%;

  object-fit: cover;

  border-radius: 10px;

  filter: brightness(0.8);
  filter: grayscale(0.3);

  transition: filter 250ms ease-in-out, box-shadow 250ms ease-in-out;
}
