.contacts-btn__button-loc {
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  align-items: center;
  text-align: center;
  width: 198px;
  height: 44px;
  color: var(--secondary-text-color);
  background: #d41342;
  border: none;
  border-radius: 22px;
  transition: background-color 280ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    background: #c7133e;
  }
}

.contacts-btn__button-far {
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  align-items: center;
  text-align: center;
  width: 198px;
  height: 44px;
  color: #d41443;
  background: var(--secondary-text-color);
  border: none;
  border-radius: 22px;
  transition: background-color 280ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    background: #ecebeb;
  }
}
